body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center {
  text-align: center;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.img-container {
  height: 400px;
  width: 600px;
  margin-left: 375px;
}

img {
  width: 100%;
  height: 100%;
}

.container {
  text-align: center;
  margin-top: 30px;
}

.button {
  padding: 20px;
  border: 2px solid #aaaaaa;
  border-radius: 10px;
  margin-top: 50px;
  cursor: pointer;
  font-size: 20px;
}

.button:hover {
  background-color: #aaa;
}

.quotes {
  background-color: #aaaaaa;
  padding: 50px;
}

h2 {
  font-size: 40px;
}

.button1 {
  float: left;
  text-align: center;
  padding: 30px;
  margin-top: 200px;
  margin-left: 400px;
  border: 2px solid #0d4399;
  border-radius: 50px;
  font-size: 25px;
  background-color: #ffffff;
  cursor: pointer;
}

.button2 {
  float: right;
  text-align: center;
  padding: 30px;
  margin-top: 200px;
  margin-right: 400px;
  border: 2px solid #0d4399;
  border-radius: 50px;
  font-size: 25px;
  background-color: #ffffff;
  cursor: pointer;
}

.button1:hover,
.button2:hover {
  background-color: #0d4399;
  color: #fff;
}

